<template lang="html">
  <div class="">
    <d-header></d-header>
    <district-list :district="district"></district-list>
  </div>
</template>

<script>
import districtList from './components/districtList.vue'
import dHeader from './components/dHeader.vue'
import axios from 'axios'
export default {
  name: 'district',
  components: {
    districtList,
    dHeader
  },
  data () {
    return {
      district: []
    }
  },
  mounted () {
    this.getDistrictInfo()
  },
  methods: {
    getDistrictInfo () {
      axios.get('/static/wcity.json').then(this.getDistrictInfoSucc)
    },
    getDistrictInfoSucc (res) {
      res = res.data
      const curid = this.$store.state.city.index - 1
      if (res.ret && res.data) {
        const data = res.data
        this.district = data[curid].district
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
</style>



