<template lang="html">
  <div class="">
    <city-list :cities="cities"></city-list>
  </div>
</template>

<script>
import cityList from './components/cityList.vue'
import axios from 'axios'
export default {
  name: 'city',
  components: {
    cityList
  },
  data () {
    return {
      cities: {}
    }
  },
  mounted () {
    this.getCityInfo()
  },
  methods: {
    getCityInfo () {
      axios.get('/static/wcity.json').then(this.getCityInfoSucc)
    },
    getCityInfoSucc (res) {
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        this.cities = data
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
</style>



