<template lang="html">
  <div class="list" ref='wrapper'>
      <div class="area">
        <div class="title border-topbottom">当前区域</div>
        <div class="button-list">
          <div class="button">
            {{this.$store.state.city.district}}
          </div>
        </div>
      </div>

      <div class="area">
        <div class="title border-topbottom">区域选择</div>
        <div class="button-list">
          <div
            class="button border-bottom"
            v-for="item of district"
            :key='item.id'
            @click="changeDistrict({name: item.dName, id: item.id})">
            {{item.dName}}
          </div>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  name: 'districtList',
  props: {
    district: Array
  },
  methods: {
    changeDistrict (district, districtid) {
      this.$store.commit('changeDistrict', district, districtid)
      this.$router.push('./')
    }
  }
}
</script>

<style lang="stylus" scoped>
  .title
    background: #eee
    color: #555
    text-align: left
    line-height: .54rem
    font-size: .32rem
    padding-left: .2rem
  .border-topbottom
    &:before
      border-color: #ccc
    &:after
      border-color: #ccc
  .border-bottom
    &:before
      border-color: #ccc
  .list
    overflow: hidden
    position: absolute
    top: 1rem
    right: 0
    left: 0
    bottom: 0
    .button-list
      overflow: hidden
      margin: .1rem .1rem .1rem .1rem
      .button
        border: .02rem solid #ccc
        float: left
        text-align: center
        width: 28%
        height: .88rem
        line-height: .88rem
        font-size: .33rem
        margin: .15rem
        padding: .1rem 0
        border-radius: .1rem
        background: #f1f3f5
        letter-spacing: .03rem
</style>



