<template lang="html">
  <div class="position">
    <div class="change-position">
      <router-link to='/city'>
        <div class="cur-position">
          {{this.$store.state.city.name}}
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'position'
}
</script>

<style lang="stylus" scoped>
.position
  padding: 0
  position: absolute
  bottom: 0
  left: 0
  right: 0
  padding: 0
  .change-position
    border-radius: 50%
    background: #000
    opacity: 0.7
    margin: .2rem auto
    height: 2.6rem
    width: 2.6rem
    .cur-position
      color: #fff
      font-size: .6rem
      text-align: center
      height: 2.6rem
      line-height: 2.6rem
      letter-spacing: .1rem
</style>



