<template lang="html">
  <div class="city">
    <div
    class="city-item"
    v-for="(item,index) of cities"
    @click="changeCity(item)"
    :key="index">
      {{item.name}}
    </div>
  </div>
</template>

<script>
export default {
  name: 'cityList',
  props: {
    cities: ''
  },
  methods: {
    changeCity (city) {
      this.$store.commit('changeCity', city)
      this.$router.push('./')
    }
  }
}
</script>

<style lang="stylus" scoped>
html
  position: relative
.city
  margin: auto
  padding: 0
  position: absolute
  top: 0
  right: 0
  left: 0
  bottom: 0
  height: 11rem
  .city-item
    border: .1rem
    border-radius: .4rem
    border-color: #9e9e9e
    font-size: .4rem
    color: #fff
    height: 1.5rem
    margin: 1rem
    background: #eee
    color: black
    line-height: 1.5rem
    text-align: center
</style>



