let defaultCity = {
  index: '02',
  name: '上海',
  cid: 'CN101020100',
  defaultDistrict: '静安区',
  defaultDid: 'CN101021400'
}

try {
  if (localStorage.city) {
    defaultCity = JSON.parse(localStorage.city)
  }
} catch (e) {}

export default {
  city: defaultCity,
  district: defaultCity.defaultDistrict,
  defaultDid: defaultCity.defaultDid
}



