<template lang="html">
  <div class="weather">
    <div class="temprature">
      {{ temprature }}℃
    </div>
    <div class="content">
      <div class="title">
        预报
      </div>
      <div class="c-item">
        <table>
        <tr>
          <td>{{ today.date }}</td>
          <td>{{ today.cond_txt_d }}</td>
          <td>{{ today.tmp_max }}℃</td>
          <td>{{ today.tmp_min }}℃</td>
        </tr>
        <tr>
          <td>{{ tomorrow.date }}</td>
          <td>{{ tomorrow.cond_txt_d }}</td>
          <td>{{ tomorrow.tmp_max }}℃</td>
          <td>{{ tomorrow.tmp_min }}℃</td>
        </tr>
        <tr>
          <td>{{ afterTomorrow.date }}</td>
          <td>{{ afterTomorrow.cond_txt_d }}</td>
          <td>{{ afterTomorrow.tmp_max }}℃</td>
          <td>{{ afterTomorrow.tmp_min }}℃</td>
        </tr>
        </table>
      </div>
    </div>
    <div class="content">
      <div class="title">
        空气质量
      </div>
      <div class="c-item">
        <div class="c-content">
          空气质量指数:
          <div class="air-content">
            {{ airCondition.aqi }}
          </div>
        </div>
        <div class="c-content">
          空气质量:
          <div class="air-content">
            {{ airCondition.qlty }}
          </div>
        </div>
        <div class="c-content">
          主要污染物:
          <div class="air-content">
            {{ airCondition.main }}
          </div>
        </div>
        <div class="c-content">
          PM2.5:
          <div class="air-content">
            {{ airCondition.pm25 }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Weather',
  data () {
    return {
      temprature: '',
      tomorrow: {},
      today: {},
      afterTomorrow: {},
      airCondition: {}
    }
  },
  mounted () {
    var fid = this.$store.state.city.defaultDid
    var cid = this.$store.state.city.cid
    console.log(cid)
    /*
    axios.get('https://free-api.heweather.com/s6/weather?location=' + fid + '&key=9b31c7f9a2b94524a6cc3a0596c9f345')
      .then(response => {
        this.temprature = response.data.HeWeather6[0].now.tmp
        this.dailyForecast = response.data.HeWeather6[0].daily_forecast
        this.today = this.dailyForecast[0]
        this.tomorrow = this.dailyForecast[1]
        this.afterTomorrow = this.dailyForecast[2]
        console.log(this.temprature)
        console.log(this.dailyForecast)
      })
        */
    /*
    axios.get('https://free-api.heweather.com/s6/air/now?' + fid + '&key=9b31c7f9a2b94524a6cc3a0596c9f345')
      .then(response => {
        this.air = response.data.HeWeather6[0].air_now_city
      })
        */
    axios.all([
      axios.get('https://free-api.heweather.com/s6/weather?location=' + fid + '&key=9b31c7f9a2b94524a6cc3a0596c9f345'),
      axios.get('https://free-api.heweather.com/s6/air/now?location=' + cid + '&key=9b31c7f9a2b94524a6cc3a0596c9f345')
    ]).then(axios.spread((weather, air) => {
      // 上面两个请求都完成后，才执行这个回调方法
      this.temprature = weather.data.HeWeather6[0].now.tmp
      console.log(air.data)
      this.dailyForecast = weather.data.HeWeather6[0].daily_forecast
      this.today = this.dailyForecast[0]
      this.tomorrow = this.dailyForecast[1]
      this.afterTomorrow = this.dailyForecast[2]
      this.airCondition = air.data.HeWeather6[0].air_now_city
    }))
  }
}
</script>

<style lang="stylus" scoped>
.weather
  position: relative
  top: 1rem
  height: 10rem
  color: white
  font-size: .33rem
  letter-spacing: .05rem
  .temprature
    text-align: right
    font-weight: bold
    font-size: 1rem
    margin: .3rem .2rem
  .content
    height: 3.5rem
    background: #000
    opacity:0.7
    margin: .4rem .3rem
    .title
      text-align: center
      margin: .1rem
      padding: .3rem .2rem 0 .44rem
    .c-item
      text-align: center
      table
        border-collapse: separate
        width: 340px
        height: 136px
        text-align: center
      .c-content
        margin: .1rem;
        text-align: left;
        padding: .14rem .44rem;
        .air-content
          float: right;
</style>



