<template lang="html">
  <div class="header">
    <router-link to='/district'>
      <div class="header-right">
        {{this.$store.state.city.district}}
      </div>
    </router-link>
  </div>
</template>

<script>
export default {
  name: 'homeHeader'
}
</script>

<style lang="stylus" scoped>
  .header
    position: absolute
    top: 0
    right: 0
    left: 0
    height: 1rem
    color: #fff
    font-size: .4rem
    line-height: .88rem
    padding: 0
    opacity: 0.8
    .header-right
      margin: .12rem .2rem
      text-align: center
      color: #000
      font-size: .4rem
      line-height: .8rem
      font-weight: bold
      .icon-down
        font-size: .2rem
        margin-left: -0.02rem
</style>



